.footer-container {
    background-color: #222831;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80px;
  }
  
  /* Social Icons */
  .social-icon-link {
    color: #fff;
    font-size: 24px;
  }
  
  .social-media {
    max-width: 1250px;
    width: 100%;
  }
  
  .social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1250px;
    margin: auto;
  }
  
  .social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 20%;
    margin-top: 10px;
  }
  
  .social-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
  
  .website-rights {
    color: #fff;
    font-size: 1rem;
    margin-top: 10px;
  }
  
  @media screen and (max-width: 500px) {
    .social-media-wrap {
      flex-direction: column;
    }
    .social-icons {
      width: 30%;
    }
  }