.columnform{
  display: flex;
  flex-direction: column;
  margin-bottom: 10%;
}
.inputformcontainer{
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 14px;
}

.label{
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
  color: #424242;
}



  .inputform::placeholder{
       font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 14px;
    color: #C4C4C4;
  }
  .inputform:focus{
    color: #424242;
    border: 0.5px solid #E52C32;
    box-shadow: 0px 0px 5px rgba(229, 44, 50, 0.25);
  }

  .passinputformcontainer{
    display: flex;
    align-items: center;
    justify-content: center;
       font-family: "Nunito Sans", sans-serif;
    font-weight: 600;
    font-size: 20px;
    position: relative;

  }
  .passicon{
    position:absolute;
  right: 5%;
  }

  input:focus { 
    border: transparent;
    outline: none  !important;;
  }

  .is-invalid{
    background-color: #fff;
    color: #424242;
  }
.errorcontainer{
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5%;
}
  .error{
    display: flex;
  
    justify-content: flex-start;
       font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
  color: #DF3030;

  }